.container {
  background-color: $green-page-color;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  opacity: 0;
  pointer-events: none;
  height: 100%;

  overflow: auto;
  z-index: 1;
  transition: max-height .3s ease-in-out,opacity .3s ease-in-out;

   &:target {
    opacity: 1;
    pointer-events: all;
   }

   @include content-padding-inline;

   &__menu {
    justify-content: space-between;
    align-items: center;
    padding: 30px 0;
    display: flex;
   }

   &__form1 {
    display: flex;
    gap: 43px;

    @include on-tablet {
      display: none;
    }

    @include on-desktop {
      display: none;
    }
   }

    &__form2 {
      display: none;

      @include on-tablet {
        display: flex;
        gap: 44px;
      }

      @include on-desktop {
        display: flex;
        gap: 44px;
      }
    }

  &__wraper {

    @include on-tablet {
      display: flex;
      margin: 30px 0 50px;
      gap: 123px;
    }

    @include on-desktop {
      display: flex;
      margin: 40px 0 70px;
      gap: 83px;
    }
  }

  &__language{
    display: flex;
    font-family: "Raleway", sans-serif;
    font-weight: 500;
    font-size: 16px;
    width: 36px;
    line-height: 150%;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: $white-color;
    font-style: normal;
    cursor: pointer;
    gap: 5px;
    right: 20px;

    @include hover (transform, scale(1.05));

    &::after {
      content: "";
      background-image: url(../images/icons/icon--arrow-to-down.png);
      display: block;
      width: 8px;
      background-position: 50%;
      background-repeat: no-repeat;
      padding-bottom: 20px;
    }
  }

  &__close-icon {
    background-image: url(../images/icons/icon--close.png);
    background-repeat: no-repeat;
    width: 22px;
    height: 22px;
    display: block;

    @include hover (transform, scale(1.05));

  }

  &__schedule {
    color: $white-color;
    margin: 30px 0 20px;

    @include on-tablet {
      margin: 0;
    }

  }

  &__schedule-day {
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;

    @include on-tablet {
      width: 117px;
    }

    @include on-desktop {
      width: 224px;
    }
  }

  &__schedule-time {
    font-family: "Raleway", sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
  }

  &__adress {
    color: $white-color;
    margin-bottom: 40px;

    @include on-tablet {
      margin: 0;
    }
  }

  &__adress-adres {
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
  }

  &__adress-street {
    font-family: "Raleway", sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
  }

  &__title {
    color: $white-color;
    font-family: "Playfair Display", serif;
    font-weight: 700;
    font-style: normal;
    font-size: 20px;
    line-height: 150%;
    list-style: none;

    @include on-tablet {
      font-size: 48px;
      line-height: 120%;
    }
  }

  &__title-m {
    margin: 20px 0 20px;

    @include on-tablet {
      margin: 30px 0 30px;
    }

    @include on-desktop {
      margin: 60px 0 60px;
    }
  }

  &__title-exhibition {
    position: relative;
    color: $white-color;
    text-decoration: none;
    width: 200px;
    transition: all 0.5s;

    &::before {
      content: '';
      position: absolute;
      left: 0; bottom: -5px;
      width: 0;
      height: 1px;
      background-color: $white-color;
    }

    &:hover {
      color: #a0a0a0;
    }

    &:hover:before {
      width: 100%;
    }

    &::before {
      transition: width 0.35s;
    }

    @include on-tablet {
      width: 480px;
    }

    @include on-desktop {
      width: 480px;
    }
  }

  &__title-poster {
    margin: 20px 0 20px;
    position: relative;
    color: $white-color;
    text-decoration: none;
    width: 165px;
    transition: all 0.5s;

    &::before {
      content: '';
      position: absolute;
      left: 0; bottom: -5px;
      width: 0;
      height: 1px;
      background-color: $white-color;
    }

    &:hover {
      color: #a0a0a0;
    }

    &:hover:before {
      width: 100%;
    }

    &::before {
      transition: width 0.35s;
    }

    @include on-tablet {
      margin: 30px 0 30px;
      width: 390px;
    }

    @include on-desktop {
      margin: 60px 0 60px;
      width: 390px;
    }
  }

  &__title-news {
    position: relative;
    color: $white-color;
    text-decoration: none;
    width: 80px;
    transition: all 0.5s;


    &::before {
      content: '';
      position: absolute;
      left: 0; bottom: -5px;
      width: 0;
      height: 1px;
      background-color: $white-color;
    }

    &:hover {
      color: #a0a0a0;
    }

    &:hover:before {
      width: 100%;
    }

    &::before {
      transition: width 0.35s;
    }

    @include on-tablet {
      width: 190px;
    }

    @include on-desktop {
      width: 190px;
    }
  }

  &__line {
    height: 1px;
    background-color: $white-color;
    margin: 40px 0 40px;
    grid-column: 1/-1;

    @include on-tablet {
      margin: 50px 0 50px;
    }

    @include on-desktop {
      margin: 60px 0 60px;
      width: 570px;
    }
  }

  &__tickets-link {
    display: flex;
    font-family: "Raleway", sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    height: 16px;
    padding: 22px 0 22px;
    color: $white-color;
    background-color: $main-color ;
    font-size: 16px;
    font-style: normal;
    line-height: 60px;
    letter-spacing: 0.32px;
    cursor: pointer;
    width: 280px;

    @include hover (transform, scale(1.05));

    &:hover {
     background-color: $hover-color;
     color: $white-color;
     }

    &:focus {
      background-color: $pressed-color;
      color: $white-color;
    }

    @include on-tablet {
      width: 330px;
      margin-bottom: 30px;
    }

    @include on-desktop {
      width: 270px;
    }
  }

  &__boy-img {

    @include on-tablet {
      background-image: url(../images/header--boy.svg);
      width: 422px;
      height: 466px;
      background-repeat: no-repeat;
      background-size: contain;
      right: 50%;
      position: absolute;
      transform: translateX(50%);
    }

    @include on-desktop {
      width: 624px;
      height: 697px;
      bottom: 0;
      right: 0;
      transform: translateX(0);
    }
  }
}
