.exhibition {
  background-color: $green-page-color;
  height: 416px;
  margin-bottom: 100px;
  display: flex;

    @include on-tablet {
      padding-bottom: 80px;
      height: 515px;
    }

    @include on-desktop {
      height: 800px;
      padding-bottom: 0;
      margin-bottom: 0;
    }

    &__box {

      @include content-padding-inline;

      @include on-desktop {
        max-width: 670px;
        text-align: left;
        padding-inline: 0;
      }
    }

  &__img {

    @include on-desktop {
      background-image: url(../images/exhibition__img.svg);
      height: 800px;
      width: 578px;
      background-repeat: no-repeat;
      background-position: 0 100%;
      min-height: 800px;
    }
  }

  &__title {
    color: $white-color;
    font-family: "Playfair Display", serif;
    font-weight: 700;
    font-style: normal;
    font-size: 38px;
    padding-top: 50px;
    margin-bottom: 20px;
    line-height: 100%;

    @include on-tablet {
      font-size: 62px;
      padding-top: 80px;
      margin-bottom: 60px;
    }

    @include on-desktop {
      font-size: 80px;
      padding-top: 205px;
      margin-bottom: 50px;
    }
  }

  &__description {
    color: $white-color;
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;

    @include on-tablet {
      width: 450px;
    }
  }

  &__tickets-link {
    display: flex;
    font-family: "Raleway", sans-serif;
    font-weight: 600;
    font-size: 16px;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    height: 16px;
    padding: 22px 0 22px;
    color: $white-color;
    background-color: $main-color ;
    font-size: 16px;
    font-style: normal;
    width: 270px;
    line-height: 60px;
    letter-spacing: 0.32px;
    cursor: pointer;
    margin: 40px 0 50px;

    @include hover (transform, scale(1.05));

    &:hover {
     background-color: $hover-color;
     color: $white-color;
     }

    &:focus {
      background-color: $pressed-color;
      color: $white-color;
    }

    @include on-tablet {
      width: 330px;
      margin-top: 60px;
    }

    @include on-desktop {
      width: 270px;
      margin-top: 50px;
    }
  }
}







